@use "../../../bulma/sass/utilities" as bulma-utilities;
@use "../../../bulma/sass/elements/table" as bulma-table;
@use "../utilities/functions";
@use "../../../bulma/sass/utilities/mixins-scss" as bulma-mixins-scss;

$table-sticky-header-height: 300px !default;

// Map Local variables
$-css-vars-map: (
    "table-sticky-header-height": $table-sticky-header-height,
);

// Register & Export CSS Variables
@include bulma-utilities.exportCSSVars($-css-vars-map);
@include bulma-utilities.registerComponentCSSVars("b-table", $-css-vars-map);

@mixin table-cards {
    .table {
        background-color: transparent;
    }
    thead {
        tr {
            border-width: 0;
            box-shadow: none;
            th {
                display: none;
            }
            .checkbox-cell {
                display: block;
                width: 100%;
                border: 0;
                @include bulma-utilities.logical-y(margin, 1rem, $bottom: true);
                text-align: end;
            }
        }
    }
    tfoot {
        th {
            display: inherit;
            border: 0; // Disable is-bordered
        }
    }
    tr {
        position: relative;
        display: block;
        max-width: 100%;
        // Card style — Cannot extend inside media query
        box-shadow: 0 2px 3px #{bulma-mixins-scss.set-color-alpha(var(--scheme-invert), 0.1)},
			0 0 0 1px #{bulma-mixins-scss.set-color-alpha(var(--scheme-invert), 0.1)};
        td {
            display: inherit;
            border: 0; // Disable is-bordered
            &:last-child {
                border-bottom: 0;
            }
        }
        &:not(:last-child) {
            @include bulma-utilities.logical-y(margin, 1rem, $bottom: true);
        }
        // Disables is-*
        &:not([class*="is-"]) {
            background: var(--table-background-color);
            &:hover {
                background-color: var(--table-background-color);
            }
        }
        &.detail {
            @include bulma-utilities.logical-y(margin, -1rem, $bottom: false);
        }
    }
    tr:not(.detail, .is-empty, .table-footer) {
        td {
            display: flex;
            width: auto;
            justify-content: space-between;
            border-bottom: 1px solid var(--background);
            text-align: end;
            &::before {
                @include bulma-utilities.logical-x(padding, 0.5em, $right: true);
                content: attr(data-label);
                font-weight: var(--weight-semibold);
                text-align: start;
            }
        }
    }
}

.table-wrapper {
    .table {
        @include bulma-utilities.logical-y(margin, 0, $bottom: true);
    }
    &:not(:last-child) {
        @include bulma-utilities.logical-y(margin, 1.5rem, $bottom: true);
    }
    @include bulma-utilities.touch {
        overflow-x: auto;
    }
}

.b-table {
    transition: opacity var(--speed) var(--easing);

    .table-mobile-sort {
        @include bulma-utilities.tablet {
            display: none;
        }
    }

    .icon {
        transition: transform calc(var(--speed) * 2) var(--easing), opacity var(--speed) var(--easing);
        &.is-desc {
            transform: rotate(180deg);
        }
        &.is-expanded {
            transform: rotate(90deg);
        }
    }

    .sort-icon.icon.is-desc {
        transform: rotate(180deg) translateY(-50%) !important;
    }

    .table {
        width: 100%;
        border: 1px solid transparent;
        border-radius: var(--radius);
        border-collapse: separate;
        th {
            font-weight: var(--weight-semibold);
            .th-wrap {
                display: flex;
                align-items: center;
                .icon {
                    @include bulma-utilities.logical-x(margin, 0.5em 0);
                    font-size: 1rem;
                }
                &.is-numeric {
                    width: 95%;
                    flex-direction: row-reverse;
                    text-align: end;
                    .icon {
                        @include bulma-utilities.logical-x(margin, 0 0.5em);
                    }
                }
                &.is-centered {
                    justify-content: center;
                    text-align: center;
                }
            }
            &.is-current-sort {
                border-color: var(--grey);
                font-weight: var(--weight-bold);
            }
            &.is-sortable:hover {
                border-color: var(--grey);
            }
            &.is-sortable,
            &.is-sortable .th-wrap {
                cursor: pointer;

                .is-relative {
                    position: absolute;
                }
            }
            .sort-icon, .multi-sort-cancel-icon {
                position: absolute;
                bottom: 50%;
                @include bulma-utilities.logical-x(inset, 100%, $right: false);
                transform: translateY(50%);
            }
            .multi-sort-cancel-icon {
                @include bulma-utilities.logical-x(margin, 0.875rem, $right: false);
            }
            &.is-sticky {
                position: sticky;
                z-index: 3 !important;
                @include bulma-utilities.logical-x(inset, 0, $right: false);
                background: var(--table-head-background-color);
            }
        }
        tr {
            &.is-selected {
                .checkbox input {
                    &:checked + .check {
                        background: var(--table-row-active-color); // url(functions.checkmark(bulma-table.$table-row-active-background-color)) no-repeat center center;
                        color: bulma-table.$table-row-active-background-color;

                        &::after {
                            position: absolute;
                            top: 0;
                            content: "✓"
                        }
                    }
                    + .check {
                        border-color: var(--table-row-active-color);
                    }
                }
            }
            &.is-empty:hover {
                background-color: transparent;
            }
        }
        .chevron-cell {
            vertical-align: middle;
            > a {
                color: var(--input-arrow-color) !important;
            }
        }
        .checkbox-cell {
            width: 40px;
            .checkbox {
                vertical-align: middle;
                .check {
                    transition: none;
                }
            }
        }
        tr.detail {
            background: var(--scheme-main-bis);
            box-shadow: inset 0 1px 3px var(--grey-lighter);
            .detail-container {
                padding: 1rem;
            }
        }
        &:focus {
            border-color: var(--link);
            box-shadow: 0 0 0 0.125em #{bulma-mixins-scss.set-color-alpha(var(--link), 0.25)};
        }

        // Modifiers
        &.is-bordered {
            th.is-current-sort,
            th.is-sortable:hover {
                border-color: var(--grey-lighter);
                background: var(--background);
            }
        }

        td {
            &.is-sticky {
                position: sticky;
                z-index: 1;
                @include bulma-utilities.logical-x(inset, 0, $right: false);
                background: var(--table-background-color);
            }
        }

        &.is-striped {
            tbody {
                tr:not(.is-selected) {
                    &:nth-child(even) {
                        td {
                            &.is-sticky {
                                background: var(--table-striped-row-even-background-color);
                            }
                        }
                    }
                }
            }
        }
    }

    .level:not(.top) {
        @include bulma-utilities.logical-y(padding, 1.5rem, $bottom: true);
    }

    .table-wrapper {
        position: relative;
        &.has-sticky-header {
            height: var(--table-sticky-header-height);
            overflow-y: auto;
            &.has-mobile-cards {
                @include bulma-utilities.mobile {
                    height: initial !important;
                    overflow-y: initial !important;
                }
            }
            tr:first-child {
                th {
                    position: sticky;
                    z-index: 2;
                    top: 0;
                    background: var(--table-background-color);
                }
            }
        }
        &.has-mobile-cards {
            @include bulma-utilities.mobile {
                @include table-cards
            }
        }
        &.is-card-list {
            @include table-cards
        }
    }
}
